import type { DeepPartial } from '@bemedev/types';

export function defineO<T extends object>(data?: T): NonNullable<T>;

export function defineO<T extends object>(
  data?: T,
  parts?: DeepPartial<T>,
): NonNullable<Required<T>>;

export function defineO<T extends object>(
  data?: T,
  parts?: DeepPartial<T>,
) {
  return (data ?? parts ?? {}) as NonNullable<Required<T>>;
}
